import React from 'react';
import styled from 'styled-components';

let getColor = ({ year, type }) => {
  if (year) {
    switch (year) {
      case '2016':
        return '#4a5b8f';
      case '2017':
        return '#45a39d';
      case '2018':
        return '#518268';
      case '2019':
        return '#ab247c';
      case '2020':
        return '#8a4ba3';
      default:
        return '#888';
    }
  } else if (type) {
    switch (type) {
      case 'feature':
        return '#00c407';
      case 'talk':
        return '#e6917c';
      case 'workshop':
        return '#8d9103';
      case 'tutorial':
        return '#484963';
    }
  }
}

export default props => (
  <CardWrapper href={props.link} target="_blank">
    <CardContent>
      <CardHeader>
        {props.title}
      </CardHeader>
      <CardSubHeader>{props.content}</CardSubHeader>
      <CardTags>
        <CardTag color={getColor({ year: props.year })}>
          {props.year}
        </CardTag>
        <CardTag color={getColor({ type: props.type })}>
          {props.type}
        </CardTag>
      </CardTags>
    </CardContent>
  </CardWrapper>
);

const CardWrapper = styled.a`
  display: inline-block;
  margin: 2vh 1vw;
  border-radius: 5px;
  min-height: 100px;
  color: #444545;
  flex: 1;
  min-width: 300px;
  text-decoration: none;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.1);
  background-color: #ffffff;
  -webkit-transition: all 0.6s cubic-bezier(0.165, 0.84, 0.44, 1);
  transition: all 0.6s cubic-bezier(0.165, 0.84, 0.44, 1);

  &::after {
    content: "";
    border-radius: 5px;
    position: absolute;
    z-index: -1;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.3);
    opacity: 0;
    -webkit-transition: all 0.6s cubic-bezier(0.165, 0.84, 0.44, 1);
    transition: all 0.6s cubic-bezier(0.165, 0.84, 0.44, 1);
  }

  &:hover {
    -webkit-transform: scale(1.03, 1.03);
    transform: scale(1.03, 1.03);
  }

  &:hover::after {
    opacity: 1;
  }

  @media (pointer: coarse), (max-aspect-ratio: 1/1) {
    max-width: 70vw;
  }
`;

const CardContent = styled.div`
  display: flex;
  flex-flow: column wrap;
  justify-content: flex-start;
  align-items: flex-start;
  height: 100%;
`;


const CardHeader = styled.span`
  font-family: 'Proxima Nova Bold';
  display: inline-block;
  font-size: 1.2rem;
  margin: 2.5vh 2vw 1vh 2vw;
`;

const CardSubHeader = styled.span`
  margin: 0 2.5vw 1.5vh 2vw;
  font-size: 0.9rem;
`;

const CardTags = styled.div`
  display: flex;
  margin: auto 0 2vh 1.8vw;
  font-size: 0.8rem;
`;

const CardTag = styled.div`
  border: 1.25px solid ${props => props.color ? props.color : '#888'};
  color: ${props => props.color ? props.color : '#888'};
  border-radius: 15px;
  display: inline-block;
  padding: 1.5px 10px;
  margin-right: 5px;
  opacity: 0.6;
`;

const CardTagContent = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  
`;

const TalksHeader = styled.h1`
  margin: 0;
  font-family: 'Proxima Nova Bold';
  display: flex;
  font-size: 36px;
  background: linear-gradient(to right top, #667eea, #764ba2);
  -webkit-background-clip: text;
  -moz-background-clip: text;
  -webkit-text-fill-color: transparent;
  -moz-text-fill-color: transparent;
`;
